<template>
    <div class="top">
        <img :src="$store.state.introduction[0].src" class="logo">
        <div class="font">
            <P style="font-size:50px;margin-bottom: 10px;margin-top: 0px;">{{$store.state.introduction[0].name}}</P>
            <p>
                {{$store.state.introduction[0].intr}}
            </p>
            <el-button type="primary">关注</el-button>
        </div>
    </div>
</template>

<script>
import store from '../vuex/store'

export default {
    data() {
        return {

        }
    },
    store
}
</script>

<style scoped>
.top {
    height: 300px;
    width: 100%;
    background-color: white;
}
.logo {
    height: 260px;
    float: left;
    margin-left: 12px;
}


</style>


