</template>

<script>
export default {
  name: 'left1',
  data () {
   return {

   }
  }
}
</script>

<style scoped>
.left1 {
  float:left;
  height:90%;
  width:33%;
}
.font {
 font-size: 40px;
 position: absolute;
 z-index: 1;
 opacity:1;
 color: white;
 left: 130px ;
 top: 100px;
 font-weight: 700;
 font-size: 80px;
}
.blue {
 background-color:blue;
 height:90%;
 width: 33%;
 opacity:0.5;
  position: absolute;
  z-index: 0;
}
img {
   height:90%;
   width: 33%;
   z-index: -1;
   position: absolute;
   left: 0px;
}
.font_blue:hover {
   opacity:0;
}

</style>



