<template>
  <div id="app" >
    <!--这是顶部-->
    <router-view @search="search()" />
    <!--这是首页-->
      <router-view name="left1" v-if="showindex"/>
      <router-view name="left2" v-if="showindex"/>
      <router-view name="left3" v-if="showindex"/>
    <!--这是个人信息页-->
      <person v-if="showperson" ></person>
    <!--这是评论展示页-->
        <background v-if="showfont"></background>
    <!--数据可视化展示页-->   
        <analyse v-if="showdata"></analyse>
  </div>


</template>

<script>
import background from '../src/components2/background'
import person from '../src/components/person'

export default {
  name: 'App',
  data() {
    return{
      showindex:true,
      //showfont是评论区
      showfont:false,
      showperson:false,
      showdata:false
    }
  },
  components:{
    background,person
  },
  methods: {
    //出现查询页
    search() {
      this.showindex=false;
      this.showfont=true;
      this.showperson=false;
      this.showdata=false;
    },
    //去到首页
    goindex() {
      this.showindex=true;
      this.showfont=false;
      this.showperson=false;
      this.showdata=false;
    },
    //去到个人中心
    goperson() {
      this.showindex=false;
      this.showfont=false;
      this.showperson=true;
      this.showdata=false;
    },
    //去可视化
    goanalyse() {
      alert("s");
      this.showindex=false;
      this.showfont=false;
      this.showperson=false;
      this.showdata=true;
    }
  }
}



</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
.select1{
  position:absolute;
  right:0px;
  top:10%;
}

</style>



